input[type="checkbox"],
input[type="radio"] {
	position: absolute;
	opacity: 0;
	z-index: -1;
}
label[class="marca"] {
	position: relative;
	display: inline-block;
	margin-right: 10px;
	margin-bottom: 10px;
	padding-left: 30px;
	padding-right: 10px;
	line-height: 36px;
	cursor: pointer;
	z-index: 1;
}
label[class="marca"]::before {
	content: " ";
	position: absolute;
	top: 6px;
	left: 0;
	display: block;
	width: 24px;
	height: 24px;
	border: 2px solid #fefbff;
	border-radius: 4px;
	z-index: -1;
}
input[type="radio"] + label::before {
	border-radius: 18px;
}
/* Checked */
input[type="checkbox"]:checked + label,
input[type="radio"]:checked + label {
	padding-left: 10px;
	color: #fff;
}
input[type="checkbox"]:checked + label::before,
input[type="radio"]:checked + label::before {
	top: 0;
	width: 100%;
	height: 100%;
	/* background: #8e44ad; */
	background: rgba(255, 255, 255, 0.5);
}
/* Transition */
label,
label::before {
	transition: .25s all ease;
}

