.profile-modal {
	background-color: rgba(255, 255, 255, 0.8);
	position: fixed;
	height: 100%;
	width: 100%;
	top: 0;
	left: 0;
	display: flex;
	align-items: center;
	justify-content: center;
}

.tc {
	padding: 0;
	margin: 0;
}
.avatar {
	padding: 0;
	margin: 0;
	cursor: pointer;
	background-color: #fff;
}

.dropDownMenu {
	top: 10px !important;
	/* left: 85px !important; */
	box-sizing: border-box;
}
.dropdown-menu {
	min-width: auto;
	width: 75px;
}
.dropdown-item {
	box-sizing: border-box;
	background-color: Transparent;
	background-repeat: no-repeat;
	border: none;
	margin-bottom: 0;
	padding-bottom: 0;
	color: #222222;
	overflow: hidden;
	outline: none;
}
.dropdown-item:hover {
	text-decoration: underline;
}

.modalInfoContainer {
	display: flex;
	flex-direction: row;
	color: #ffffff;
	width: 25%;
	border-radius: 10px;
	background-color: #2e2e2e;
	border: 2px solid #ffffff;
}
.headerform {
	margin-top: 10% !important;
}
.mainModalForm {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
}
.submitsButtonModal {
	display: flex;
	flex-direction: row;
	justify-content: center;
	height: auto;
	margin-top: 0;
	max-width: 90%;
}
.buttonSubmitModal {
	width: 15vw;
	height: auto;
	cursor: pointer;
	padding: 7px;
	margin-right: 10px;
	margin-left: 10px;
	margin-bottom: 5px;
	margin-top: 5px;
	border-radius: 10px;
	color: #2e2e2e;
	background-color: rgba(255, 255, 255, 0.5);
	border: 2px solid #2e2e2e;
	-webkit-box-shadow: 0px 10px 13px -11px #000000, 5px 5px 15px 5px rgba(0, 0, 0, 0);
	box-shadow: 0px 10px 18px -11px #000000, 5px 5px 20px 5px rgba(0, 0, 0, 0);
}
.buttonSubmitModal:hover {
	background-color: rgba(255, 0, 0, 0.25);
	color: rgba(255, 255, 255, 0.5);
}

.labelForm {
	margin-top: 5px;
}

.inputform {
	margin: 5px;
	padding: 3px;
	border-radius: 3px;
	background: transparent;
	border: none;
	border-bottom: 2px solid rgba(255, 255, 255, 0.25);
}

.modal-close {
	font-size: 3rem;
	align-items: flex-end;
	font-style: bold;
	right: 25px;
	position: relative;
	cursor: pointer;
}

.modal-close:hover {
	color: grey;
	cursor: pointer;
}

.passwordInvalid {
	-webkit-box-shadow: 0px 0px 0px 5px #a0a0a0, inset 0px 10px 27px -8px #141414, inset 0px -10px 27px -8px #a31925,
		5px 5px 15px 5px rgba(0, 0, 0, 0);
	box-shadow: 0px 0px 0px 5px rgba(255, 0, 0, 0.25), inset 0px 10px 27px -8px rgba(255, 255, 255, 0.5),
		inset 0px -10px 27px -8px #a31925, 5px 5px 15px 5px rgba(0, 0, 0, 0);
	border-radius: 5px;
	animation: invalid 0.82s 2;
}
@keyframes invalid {
	10%,
	90% {
		transform: translate3d(-1px, 0, 0);
	}

	20%,
	80% {
		transform: translate3d(2px, 0, 0);
	}

	30%,
	50%,
	70% {
		transform: translate3d(-4px, 0, 0);
	}

	40%,
	60% {
		transform: translate3d(4px, 0, 0);
	}
}
@media all and (max-width: 780px) {
	.profile-modal {
		max-width: 100vw;
	}
	.modalInfoContainer {
		width: 75vw;
	}
	.buttonSubmitModal {
		width: 35vw;
	}
}
