input[type="checkbox"],
input[type="radio"] {
	position: absolute;
	opacity: 0;
	z-index: -1;
}
label[class="services"] {
	position: relative;
	display: inline-block;
	margin-right: 10px;
	margin-bottom: 10px;
	padding-left: 25px;
	padding-right: 10px;
	line-height: 36px;
	cursor: pointer;
	/* z-index: 1000; */
	z-index: 1;
}
label[class="services"]::before {
	content: " ";
	position: absolute;
	top: 6px;
	left: 0;
	display: block;
	width: 24px;
	height: 24px;
	/* border: 2px solid #fefbff; */
	border: 2px solid #2e2e2e;
	border-radius: 4px;
	z-index: -1;
	background: #fff;
}
input[type="radio"] + label::before {
	border-radius: 18px;
}
/* Checked */
input[type="checkbox"]:checked + label[class="services"],
input[type="radio"]:checked + label[class="services"] {
	padding-left: 10px;
	color: #fff;
}
input[type="checkbox"]:checked + label[class="services"]::before,
input[type="radio"]:checked + label[class="services"]::before {
	top: 0;
	width: 100%;
	height: 100%;
	background: #2e2e2e;
}
/* Transition */
label,
label::before {
	-webkit-transition: .25s all ease;
	-o-transition: .25s all ease;
	transition: .25s all ease;
}

/* Level */
