	z-index: 1;
}
label[class="interruptionLevel"]::before {
	content: " ";
	position: absolute;
	top: 6px;
	left: 0;
	display: block;
	width: 24px;
	height: 24px;
	border: 2px solid #2e2e2e;
	border-radius: 4px;
	z-index: -1;
	background: #fff;
}
input[type="radio"] + label::before {
	border-radius: 18px;
}
/* Checked */
input[type="checkbox"]:checked + label[class="interruptionLevel"],
input[type="radio"]:checked + label[class="interruptionLevel"] {
	padding-left: 10px;
	color: #fff;
}
input[type="checkbox"]:checked + label[class="interruptionLevel"]::before,
input[type="radio"]:checked + label[class="interruptionLevel"]::before {
	top: 0;
	width: 100%;
	height: 100%;
	background: #2e2e2e;
}

