.bridgeComponent {
	width: 100vw;
	height: 34vh;
	border-radius: 5px;
	-webkit-box-shadow: 0px 10px 13px -11px #000000, 5px 5px 15px 5px rgba(0, 0, 0, 0);
	box-shadow: 0px 10px 18px -11px #000000, 5px 5px 20px 5px rgba(0, 0, 0, 0);
	/* background-color: #cff; */
}

.svg-content {
	display: inline-block;
	position: absolute;
	top: 0;
	left: 0;
}
.tooltip {
	background-color: #555;
	color: #fff;
	text-align: center;
	border-radius: 6px;
	padding: 8px 10px 10px 10px;
	position: absolute;
	z-index: 1;
	/* bottom: 125%;
    left: 50%; */
	/* margin-left: -80px; */
}
.tooltip::after {
	content: "";
	position: absolute;
	top: 100%;
	left: 50%;
	margin-left: -5px;
	border-width: 5px;
	border-style: solid;
	border-color: #555 transparent transparent transparent;
}

