.legend {
	background: #cff;
	border: 1px dashed black;
	cursor: pointer;
}
.legend:hover {
	background: #acc;
	border: 1px solid black;
}

.minimap {
	height: 57.5vh;
	width: 49.5vw;
	margin: 1px 2px 2px 2px;
	/* background-color: red; */
	-webkit-box-shadow: 0px 10px 13px -11px #000000, 5px 5px 15px 5px rgba(0, 0, 0, 0);
	box-shadow: 0px 10px 18px -11px #000000, 5px 5px 20px 5px rgba(0, 0, 0, 0);
	align-content: center;
	align-items: center;
	align-self: center;
	text-align: center;
	display: flex;
	flex-direction: column;
}
.containersa {
	display: flex;
	flex-direction: row;
	max-width: 100vw;
}

@media all and (max-width: 768px) {
	.containersa {
		flex-direction: column;
		max-width: 100vw;
	}
	.minimap {
		height: 57.5vh;
		width: 98vw;
		margin: 1px 2px 2px 2px;
		/* background-color: red; */
		-webkit-box-shadow: 0px 10px 13px -11px #000000, 5px 5px 15px 5px rgba(0, 0, 0, 0);
		box-shadow: 0px 10px 18px -11px #000000, 5px 5px 20px 5px rgba(0, 0, 0, 0);
	}
}
