.listNav {
	position: relative;
	list-style: none;
	width: 100%;
	margin: 0;
	display: -webkit-box;
	display: -moz-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	flex-direction: row;
	background: #2e2e2e;
	color: rgba(255, 255, 255, 0.5);
	align-items: center;
}
.listNav li {
	text-decoration: none;
	display: block;
	border-radius: 5px;
	padding: 0.5em;
	color: rgba(255, 255, 255, 0.5);
	cursor: pointer;
}
.listNav li a {
	text-decoration: none;
	display: block;
	border-radius: 5px;
	color: rgba(255, 255, 255, 0.5);
	cursor: pointer;
}
.listNav li :hover {
	color: whitesmoke;
}

.headerItem:hover {
	color: #2e2e2e;
	background-color: rgba(255, 255, 255, 0.5);
	border-radius: 5px;
}
.headerItemRight {
	margin-left: auto;
}

.listNav .icon {
	display: none;
}

/* @media all and (max-width: 800px) {
  .listNav {
    justify-content: space-around;
  }
